<template>
  <swiper
    :slidesPerView="1"
    :spaceBetween="0"
    :loop="true"
    :slideToClickedSlide="true"
    :centeredSlides="true"
    :navigation="true"
    class="d-flex justify-content-center align-items-center"
  >
    <swiper-slide
      v-for="(slide, index) in item.items"
      :key="index"
      class="d-flex justify-content-center align-items-center"
      :id="index"
    ><div class="row d-flex justify-content-center align-items-center" :style="imageRatio(slide.image1)">
      <div class="col-12 d-flex justify-content-center" >
        <img
          data-expand="0"
          :data-src="slide.image1.url"
          alt=""
          class="lazyload img-fluid img-background"
          :height="slide.image1.dimensions.height"
          :width="slide.image1.dimensions.width"
        />
      </div>
      <div class="col-12 d-flex justify-content-start caption">{{slide.image1.alt}}</div>
      </div>
    </swiper-slide>
  </swiper>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/swiper-bundle.css";

export default {
  name: "CarouselSlice",
  components: {
    Swiper,
    SwiperSlide,
  },
  props: {
    item: Object,
  },
  methods: {
    imageRatio(e) {
      let height = e.dimensions.height;
      let width = e.dimensions.width;
      let ratio = width / height;
      let maxHeight = (window.innerHeight / 100) * 73;

      return {
        "max-height": maxHeight + "px",
        "max-width": maxHeight * ratio + "px",
      };
    },

  },
};
</script>

<style scoped>
.caption {
    color:grey;
    margin-top:0.2em;
}
.swiper-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper-button-next {
  background-image: url("../../img/shop/arrow-right.png") !important;
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
}

.swiper-button-next::after {
  display: none;
}
</style>